@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500&family=Ubuntu:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}

.App {
  height: 100%;
}

.inline-link {
  text-decoration: underline;
}

#text {
  -webkit-animation: fill 0.5s ease forwards 4s;
          animation: fill 0.5s ease forwards 4s;
}

#text path:nth-child(1) {
  stroke-dasharray: 567px;
  stroke-dashoffset: 567px;
  -webkit-animation: line-anim 2s ease forwards;
          animation: line-anim 2s ease forwards;
}

#text path:nth-child(2) {
  stroke-dasharray: 550px;
  stroke-dashoffset: 550px;
  -webkit-animation: line-anim 2s ease forwards 0.3s;
          animation: line-anim 2s ease forwards 0.3s;
}

#text path:nth-child(3) {
  stroke-dasharray: 361px;
  stroke-dashoffset: 361px;
  -webkit-animation: line-anim 2s ease forwards 0.6s;
          animation: line-anim 2s ease forwards 0.6s;
}

#text path:nth-child(4) {
  stroke-dasharray: 361px;
  stroke-dashoffset: 361px;
  -webkit-animation: line-anim 2s ease forwards 0.9s;
          animation: line-anim 2s ease forwards 0.9s;
}

#text path:nth-child(5) {
  stroke-dasharray: 544px;
  stroke-dashoffset: 544px;
  -webkit-animation: line-anim 2s ease forwards 1.2s;
          animation: line-anim 2s ease forwards 1.2s;
}

#text path:nth-child(6) {
  stroke-dasharray: 798px;
  stroke-dashoffset: 798px;
  -webkit-animation: line-anim 2s ease forwards 1.5s;
          animation: line-anim 2s ease forwards 1.5s;
}

#text path:nth-child(7) {
  stroke-dasharray: 544px;
  stroke-dashoffset: 544px;
  -webkit-animation: line-anim 2s ease forwards 1.8s;
          animation: line-anim 2s ease forwards 1.8s;
}

#text path:nth-child(8) {
  stroke-dasharray: 571px;
  stroke-dashoffset: 571px;
  -webkit-animation: line-anim 2s ease forwards 2.1s;
          animation: line-anim 2s ease forwards 2.1s;
}

#text path:nth-child(9) {
  stroke-dasharray: 361px;
  stroke-dashoffset: 361px;
  -webkit-animation: line-anim 2s ease forwards 2.4s;
          animation: line-anim 2s ease forwards 2.4s;
}

#text path:nth-child(10) {
  stroke-dasharray: 557px;
  stroke-dashoffset: 557px;
  -webkit-animation: line-anim 2s ease forwards 2.7s;
          animation: line-anim 2s ease forwards 2.7s;
}

@-webkit-keyframes line-anim {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes line-anim {
  to {
    stroke-dashoffset: 0;
  }
}

@-webkit-keyframes fill {
  from {
    fill: transparent;
  }
  to {
    fill: white;
  }
}

@keyframes fill {
  from {
    fill: transparent;
  }
  to {
    fill: white;
  }
}

@-webkit-keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

body {
  background: #291649;
}

.row {
  display: flex;
  justify-content: space-between;
}

.reveal .progress {
  /* background: rgba(0, 0, 0, 0.2); */
  color: #edc54d;
}

.reveal h1,
.reveal h2,
.reveal h3 {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 500;
}

.reveal .controls {
  color: #edc54d;
}

p,
li {
  /* font-family: 'Ubuntu', sans-serif; */
  font-family: 'Open Sans', sans-serif;

  font-size: 36px;
  line-height: 1.5;
  margin: 10px 0;
}

li::marker {
  /* content: counter(list-item) "› "; */
  color: #21685c;
}

.App {
  box-sizing: border-box; /* Opera/IE 8+ */
}

.reveal a {
  color: white;
  text-decoration: underline;
  font-style: italic;
}

.reveal pre {
  width: unset;
  padding-left: 20px;
}

/* Ensure certain elements are never larger than the slide itself */
.reveal img,
.reveal video,
.reveal iframe {
  max-width: 100%;
  max-height: 100%;
}

aside {
  margin-top: 75px;
  font-style: italic;
  font-size: 28px;
}

